export default "import React from 'react';\n\nconst RadioForm = (props) => {\n    const { selected, property, values, onChange } = props;\n    \n    return (<form className={`embla__radio-form`.concat(` embla__radio-form--${property}`)}>\n      <span className=\"embla__radio-form__label\">{property}:</span>\n \n      {values.map((value) => (<label className=\"embla__radio-wrapper\" key={value}>\n          <span className=\"embla__radio-input__wrapper\">\n            <span className=\"embla__radio-input__line-height\">-</span>\n            <input type=\"radio\" value={value} checked={value === selected} onChange={(event) => onChange(event.target.value)} name={property}/>\n          </span>\n          {value}\n        </label>))}\n    </form>);\n};\n\nexport default RadioForm;\n";